.form-wrapper {
    display: flex;    
    width: 100%;   
    width: 100%;
    height: 700px;
    background-size: cover;
    background-image: url('/src/background.jpg');
}

.form-wrapper .form-container{
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 500px;
    margin: auto;
    background: #18465d;
    border-radius: 10px;
    padding: 10px 0 30px;
}

.form-wrapper h1{
    color:#ffffff;
    text-align: center;
}

.form-wrapper .icon{
    text-align: center;
    font-size: 100px;
    color:#ffffff;
}

.form-wrapper .item-data{
    position: relative;
    margin-bottom: 15px;
}

.form-wrapper .item-data label{
    display: block;
    color:#ffffff;
    margin-bottom: 5px;
}

.form-wrapper .item-data .input-field {
    color: #040404;
    position: relative;
    background: #bce3ef;
    height: 25px;
    display: inline-block;
    border: 1px solid #4f7e8c;
    width: 200px;
    vertical-align: bottom;
}

.form-wrapper .item-data .item-input{
    position: relative;
    text-align: center;
}

.form-wrapper .item-data .item-input .item-icon{
    position: relative;
    color: #fff;
    display: inline-block;
    width: 25px;
    background: #3d97b3;
    height: 29px;
    padding: 0px 5px;
}

.form-wrapper .item-data .item-input .input-icon{
    position: relative;
    color: #fff;
    font-size: 22px;
    width: 25px;
    background: #3d97b3;
    height: 29px;
    align-items: center;
    vertical-align: middle;
}
 
.form-wrapper .btn{
    position: relative;
    background: #377b9c;
    color: #ffffff;
    padding: 8px 15px;
    font-size: 17px;
    border-radius: 5px;
    /* width: 100%; */
    border: 1px solid #93bed4;
    margin-bottom: 10px;
    margin-right: 10px;
}

.form-wrapper .btn:hover{
    background: #ffffff;
    color:#377b9c;
}

.form-wrapper .error{
    position: relative;
    color:#f19292;
    font-size: 11px;
}
